.contact-page {
  padding: 40px 10px; /* Reduced from 100px to 40px (60% reduction) */
  background-color: #171717; /* Changed from #EDEDED */
  display: flex;
  flex-wrap: wrap;
  justify-content: center; /* Centered content for alignment */
  align-items: stretch;
  gap: 40px;
  color: #EDEDED; /* Added for explicit text color */
}

.contact-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: stretch;
  gap: 30px; /* Reduced gap from 40px to 30px */
  max-width: 1600px; /* Increased max width from 1400px */
  margin: 0 auto;
  width: 95%; /* Added width property to make container take up more space */
}

.contact-form-section {
  flex: 1;
  max-width: 700px; /* Increased from 600px */
  background: #222222; /* Changed from #FFFFFF to dark gray */
  padding: 40px; /* Increased padding for more space around form elements */
  border-radius: 8px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3); /* Increased shadow opacity */
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.contact-form-section h1 {
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: #EDEDED; /* Changed from #171717 */
  text-align: center; /* Center the text */
  position: relative;
}

.contact-form-section h1:after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%; /* Start at the center */
  transform: translateX(-50%); /* Adjust for centering */
  width: 70px;
  height: 3px;
  background: #DA0037; /* Accent color */
}

.contact-form-section p {
  font-size: 1rem;
  margin-bottom: 30px;
  color: #bbbbbb; /* Lightened from #444444 for better visibility */
}

.contact-links {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 30px;
}

.contact-links a {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 1rem;
  color: #DA0037;
  text-decoration: none;
  transition: color 0.3s;
}

.contact-links a:hover {
  color: #ff1a4f;
}

.contact-links a i {
  font-size: 1.2rem;
}

.contact-form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  align-items: center; /* Ensure proper alignment */
  justify-content: center; /* Center the form elements */
  margin-top: 20px; /* Added top margin for better spacing from heading */
}

.contact-form input,
.contact-form textarea,
.contact-form select {
  width: 100%;
  padding: 15px; /* Slightly reduced padding for better proportions */
  border: 1px solid #bbbbbb; /* Lighter border color */
  border-radius: 6px; /* Slightly rounded corners for modern design */
  font-size: 1rem;
  background-color: #444444;
  color: #FFFFFF;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05); /* Reduced shadow intensity */
  box-sizing: border-box; /* Ensure padding doesn't increase element width */
}

.contact-form textarea {
  grid-column: span 2; /* Ensure textarea spans the full width */
  resize: none;
  height: 150px; /* Increased height for message box */
  margin-top: 10px; /* Added top margin for separation */
}

.contact-form button {
  grid-column: span 2; /* Ensure button spans the full width */
  padding: 15px; /* Increased padding for better clickability */
  margin-top: 10px; /* Added top margin for separation */
  background: #DA0037;
  color: #FFFFFF;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  font-weight: bold;
  transition: background 0.3s, transform 0.3s;
}

.contact-form button:hover {
  background: #ff1a4f;
  transform: translateY(-3px);
}

.contact-info-section {
  flex: 1;
  background: #FFFFFF;
  padding: 40px;
  border-radius: 8px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.contact-info-section h2 {
  font-size: 1.5rem;
  margin-bottom: 20px;
  color: #171717;
}

.contact-info-section a {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 1rem;
  color: #DA0037;
  text-decoration: none;
  margin-bottom: 15px;
}

.contact-info-section a:hover {
  text-decoration: underline;
}

.contact-info-section a i {
  font-size: 1.2rem;
}

.map-container {
  flex: 1;
  max-width: 800px; /* Increased from 700px */
  height: auto;
  min-height: 650px; /* Increased from 600px */
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.map-container iframe {
  width: 100%;
  height: 100%;
  border: none;
}

.contact-title {
  text-align: center; /* Center the title */
  font-size: 2.8rem;
  margin-bottom: 40px;
  color: #DA0037;
  width: 100%; /* Ensure it spans the full width */
  display: block; /* Ensure proper alignment */
  position: relative;
}

.contact-title:after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%; /* Start at the center */
  transform: translateX(-50%); /* Adjust for centering */
  width: 70px;
  height: 3px;
  background: #DA0037; /* Accent color */
}

.form-title {
  font-size: 1.8rem;
  margin-bottom: 20px;
  color: #DA0037;
  text-align: center;
}

.input-container {
  grid-column: span 1;
  position: relative;
  width: 100%;
}

.textarea-container {
  grid-column: span 2;
  position: relative;
  width: 100%;
}

input.error, textarea.error {
  border: 1px solid #DA0037;
}

.error-message {
  color: #DA0037;
  font-size: 0.8rem;
  position: absolute;
  bottom: -18px;
  left: 0;
}

.character-count {
  position: absolute;
  bottom: 10px;
  right: 10px;
  font-size: 0.75rem;
  color: #999;
  background: rgba(68, 68, 68, 0.7);
  padding: 2px 8px;
  border-radius: 10px;
}

.near-limit {
  color: #DA0037;
  font-weight: bold;
}

.result-message {
  display: block;
  text-align: center;
  margin-top: 15px;
  color: #EDEDED;
  font-weight: 500;
}

@media (max-width: 768px) {
  .contact-page {
    flex-direction: column;
    padding: 32px 5px; /* Reduced from 80px to 32px (60% reduction) */
  }

  .contact-container {
    flex-direction: column;
    width: 98%; /* Make almost full width on mobile */
  }

  .contact-form-section {
    padding: 30px 15px; /* Reduced padding on mobile for more content space */
    max-width: 100%; /* Take full width on mobile */
  }

  .contact-form {
    grid-template-columns: 1fr;
    gap: 15px; /* Reduced gap on mobile */
  }

  .contact-form textarea,
  .contact-form button {
    grid-column: span 1;
    margin-top: 5px; /* Less margin needed on mobile */
  }

  .contact-form input,
  .contact-form textarea,
  .contact-form select {
    padding: 12px; /* Slightly smaller padding on mobile */
  }

  .contact-form textarea {
    height: 120px; /* Slightly reduced height on mobile */
  }

  .map-container {
    min-height: 400px; /* Adjust height for mobile */
    max-width: 100%; /* Take full width on mobile */
  }

  .input-container,
  .textarea-container {
    grid-column: span 1;
  }
}